import { ref } from 'vue'

export default function touchs() {
    const img = [require('@/assets/scenery1.jpeg'), require('@/assets/scenery2.jpeg'), require('@/assets/scenery3.jpeg')]
    const start = ref(0); // ÂÅèÁßªËµ∑ÁÇπ
    const touch = ref(null); //HTMLDOM
    const move = ref(0); // ÂÅèÁßªË∑ùÁ¶ª
    const imgLength = img.length
    const imgIndex = ref(0); // ÂΩìÂâçÂ±ïÁ§∫ÂõæÁâá‰∏ãÊ†á
    // let setFun = null;
    const canMove = -(imgLength - 1) * 375 // ÂÅèÁßªÊúÄÂ§ßË∑ùÁ¶ª

    const touchend = (e) => {
        const x = e.changedTouches[0].pageX - start.value
        if (x > 0) {
            move.value += 375
            if (move.value > 0) {// Ë∂ÖÂá∫Â∑¶ËæπËåÉÂõ¥
                move.value = move.value - 355
                setTimeout(() => {
                    move.value = 0
                    changeTransform(move.value)
                }, 500)
            }
            setImgIndex('subtract')
            changeTransform(move.value)
        } else {
            move.value -= 375

            if (move.value < canMove) { // Ë∂ÖÂá∫Âè≥ËæπËåÉÂõ¥

                move.value = move.value + 355
                setTimeout(() => {
                    move.value = canMove
                    changeTransform(move.value)
                }, 500)
            }
            setImgIndex('add')
            changeTransform(move.value)
        }


    }
    const touchmove = (e) => {
        let x = e.touches[0].pageX - start.value + move.value;
        if (x > 0) {
            x = 20
        }
        if (x < canMove) {
            x = canMove - 20
        }
        changeTransform(x)

    }
    const touchstart = (e) => {
        // console.log(e)

        start.value = e.touches[0].pageX
    }

    const changeTransform = (px) => {
        touch.value.style.transform = `translate(${px / 100}rem, 0)`
    }
    const setImgIndex = (x) => {
        switch (x) {
            case 'add':
                if (imgIndex.value + 1 < imgLength) {
                    imgIndex.value++
                }
                break;
            case 'subtract':
                if (imgIndex.value - 1 >= 0) {
                    imgIndex.value--
                }
                break;
        }
    }

    return {
        img,
        imgIndex,
        touch,
        move,
        touchend,
        touchmove,
        touchstart
    }
}